/* Make entire layout fill the screen */
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
}

/* Flex column layout for page */
.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Your sections grow naturally */
.section {
  padding: 60px 20px;
  text-align: center;
}

/* Footer is pushed to bottom when content is short */
.footer {
  background-color: #0f172a;
  color: white;
  text-align: center;
  padding: 20px;
  margin-top: auto;
}
